import Vue from 'vue';
import Router from 'vue-router';
import Home from 'home/index.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta:{
        title:'赵京辉的个人网站'
      }
    },
  ],
});



